<template lang="html">
  <v-app ref='workarea'>
    <slot name="controls"></slot>
    <slot name="uploadImage"></slot>
    <main id="letteros-workarea" class="letteros-workarea" :style="viewPortCss" @drop.prevent="attachModule"
          @dragover.prevent="dragOverArea">
      <article
          class="letteros-html d-flex flex-grow-1"
          v-for="(mod, index) in moduleList"
          :key="`letter-module-${index}`"
          :ref="`module-${index}`"
          @click.prevent="moduleClickHandler($event, mod.uuid)"
          @dragover.prevent="dragOverModule($event, index)"
      >
        <slot name="default">
          <div class="d-flex flex-grow-1" v-html="mod.code"></div>
        </slot>
      </article>
    </main>
  </v-app>
</template>
<script>
import {
  VApp, // required

} from 'vuetify/lib';
import vuetify from '../plugins/vuetify';

export default {
  name: 'LetterosWorkArea',
  vuetify,
  components: {
    VApp,
  },
  props: {
    viewPort: {
      type: String,
      default: '640px',
    },
    modules: {
      type: [String, Array],
      default() {
        return ''
      }
    },
    draggableModule: {
      type: String,
      default: '',
    },
    templateModules: {
      type: Array,
      default: function () {
        return []
      }
    },
    selectedElement: {
      type: String,
      default: '',
    },
    selectedModule: {
      type: String,
      default: '',
    },
    options: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      activeModulesId: null,
      moduleList: [],
      dragEl: null,
      dragHoverIndex: null,
    };
  },
  computed: {
    viewPortCss() {
      return {
        'max-width': this.viewPort,
        width: this.viewPort,
      };
    },
    orderedModules() {
      return this.modules.slice();
    },
  },
  watch: {
    modules: function (nVal, oVal) {
      console.log('Watch prop modules: ', {nVal, oVal});
      if (nVal !== oVal && (typeof nVal === 'string')) {
        console.log('Watched prop is string', nVal);
        this.moduleList = JSON.parse(nVal)
      } else {
        console.log('Watched prop is obj:', nVal);
        this.modulesList = nVal;
      }
    }
  },
  created() {

  },
  mounted() {
    this.$nextTick(() => {
      this.$emit('workarea:readystate', {self: this.$refs.workarea});
      console.log('Workarea mounted with childs:', {refs: this.$refs, childs: this.$childs});
    });
  },
  methods: {
    deleteModule(e) {
      console.log('deleteEvent', {event: e})
      const event = new CustomEvent('workarea:delete:module', {
        detail: {uuid: e.dataTransfer.uid}
      })
      window.parent.dispatchEvent(event);
    },
    orderModule() {
    },
    attachModule(e) {
      const uuid = e.dataTransfer.getData("text/plain");
      // const moduleHover = e.path.find(element=>element.tagName === 'SECTION' && element.classList.value.includes('letteros-module'));
      // if (moduleHover && moduleHover !== this.dragEl) {
      //   event = new CustomEvent('workarea:set:module', {
      //     detail: {moduleHoverIndex: moduleHover.dataset.moduleIndex, uuid: uuid},
      //   })
      //   this.dragEl = moduleHover
      //   console.log('setEvent', {moduleHover: moduleHover, event: e, uuid: uuid})
      // } else {
      //   event = new CustomEvent('workarea:attach:module', {
      //     detail: {uuid: uuid}
      //   })
      const attchEvent = new CustomEvent('workarea:attach:module', {
        detail: {uuid: uuid, index: this.dragHoverIndex}
      })
      console.log('attachEvent', {'attachEvent': attchEvent, event: e, uuid: uuid})
      window.parent.dispatchEvent(attchEvent);
      this.deletingTemporaryModules()
      // this.dragHoverIndex = null
    },
    dragOverArea(e) {
      const moduleHover = e.path.find(element => element.tagName === 'SECTION' && element.classList.value.includes('letteros-module'));
      if (!moduleHover) {
        // this.dragHoverIndex = null
      }
    },
    dragOverModule(e, index) {
      const moduleHover = e.path.find(element => element.tagName === 'ARTICLE' && element.classList.value.includes('letteros-html'));
      if (moduleHover) {
        this.dragEl = moduleHover
        this.deletingTemporaryModules()
        let newElement = document.createElement('div');
        newElement.classList.add('letteros-temporary-modul')
        const dragModule = JSON.parse(this.draggableModule)
        if (dragModule) {
          newElement.innerHTML = JSON.parse(this.draggableModule).code
          document.getElementById("letteros-workarea").insertBefore(newElement, moduleHover)
        }
        this.dragHoverIndex = index
      } else {
        this.dragHoverIndex = null
      }
    },
    deletingTemporaryModules() {
      const removingModules = document.getElementsByClassName('letteros-temporary-modul')
      for (const removingModule of removingModules) {
        removingModule.remove()
      }
    },
    moduleClickHandler(event, uuid) {
      console.log('moduleClickHandler', {event: event, uuid: uuid})
      this.setModuleActive(uuid)
      if (event.target.tagName === 'SPAN') {
        this.selectText(uuid, event.target)
      } else if (event.target.tagName === 'IMG') {
        this.selectImage(uuid, event.target)
      } else if (event.target.tagName === 'A') {
        this.selectLink(uuid, event.target)
      }
    },
    setModuleActive(uuid) {
      console.log('selectModuleEvent', {uuid: uuid});
      const newEvent = new CustomEvent('workarea:select:module', {
        detail: {uuid: uuid}
      })
      window.parent.dispatchEvent(newEvent);
    },
    selectElement(uuid) {
      console.log('selectElementEvent', {uuid: uuid});
      const event = new CustomEvent('workarea:select:element', {
        detail: {uuid: uuid},
      })
      window.parent.dispatchEvent(event);
    },
    selectText(uuid, element) {
      const event = new CustomEvent('workarea:select:text', {
        detail: {uuid: uuid, elementObject: element},
      })
      window.parent.dispatchEvent(event);

      const selector = `span[data-text-id='${element.dataset.textId}']`
      const textNode = document.querySelector(selector)
      console.log('selectTextEvent', {uuid: uuid, element: element, selector: selector, textNode: textNode});

      textNode.setAttribute('contendeditable', true)
    },
    selectLink(uuid, element) {
      console.log('selectLinkEvent', {uuid: uuid, element: element});
      const event = new CustomEvent('workarea:select:link', {
        detail: {uuid: uuid, elementObject: element},
      })
      window.parent.dispatchEvent(event);
    },
    selectImage(uuid, element) {
      console.log('selectImageEvent', {uuid: uuid, element: element});
      const event = new CustomEvent('workarea:select:image', {
        detail: {uuid: uuid, elementObject: element},
      })
      window.parent.dispatchEvent(event);
    },
    modalImage(uuid, element) {
      const event = new CustomEvent('workarea:modal:image', {
        detail: {uuid: uuid, elementObject: element},
      })
      window.parent.dispatchEvent(event);
    },
  }
}
</script>
<style>
.letteros-workarea {
  height: 100%;
}
</style>
