$color-primary: #001dff;
$color-dark: #151515;
$color-black: #000;
$color-gray: #a9a9a9;
$color-gray-lighten: #e1e1e1;
$color-gray-lightest: #eaeaea;
$color-white: #fff;
$color-red: #d42137;
$color-green: #2d8962;
$color-yellow: #e19b33;

$body-font-family: 'Golos Text', serif;
$font-size-root: 16px;
$line-height-root: 1.56;
$border-radius-root: 15px;

$icon-size: 36px;

$toolbar-content-padding-x: 20px;
$app-bar-elevation: 0;

// внутренние отступы у текстового поля
$text-field-enclosed-details-padding: 0 15px;
$text-field-outlined-rounded-slot-padding: 0 19px;
$text-field-rounded-border-radius: $border-radius-root;

$btn-letter-spacing: 0;
$btn-text-transform: 'none';
$btn-hover-opacity: 0.21;

// аккордеон
$expansion-panel-header-font-size: 24px;
$expansion-panel-header-padding: 32px 0;
$expansion-panel-content-padding: 0 0 40px;

$progress-linear-border-radius: 12px;

$dialog-card-title-font-size: 24px;
$dialog-card-title-font-weight: 500;
$dialog-card-title-padding: 25px 30px 13px;
$dialog-card-text-padding: 0 30px;
$dialog-card-actions-padding: 0 30px 38px;
$dialog-elevation: 0;

$card-title-word-break: normal;
$card-title-line-height: 1.1;
$card-text-font-size: $font-size-root;
$card-text-letter-spacing: 0;

$navigation-drawer-border-width: 0;
$navigation-drawer-mobile-temporary-elevation: 0;

$menu-content-elevation: 0;
$menu-content-border-radius: 11px;

$switch-track-inset-width: 56px;
$switch-track-inset-height: 36px;
$switch-track-inset-border-radius: 20px;
$switch-track-inset-opacity: 1;
$switch-thumb-width: 26px;
$switch-thumb-height: 26px;
$switch-thumb-top: calc(50% - 13px);
$switch-ripple-x: -11px;

$badge-dot-width: 8px;
$badge-dot-height: 8px;

$data-table-regular-header-font-size: $font-size-root;
$data-table-regular-row-font-size: $font-size-root;

$date-picker-table-height: 320px;
$date-picker-table-font-size: 18px;

$headings: (
  'h1': (
    'size': 30px,
    'weight': 500,
    'letter-spacing': 0,
    'line-height': 1.1,
  ),
);

$btn-sizes: (
  'x-large': 56,
);

$btn-font-sizes: (
  'default': 16px,
  'x-large': 18px,
);

$rounded: (
  10: 10px,
);

$material-light: (
  'app-bar': $color-white,
  'text-fields': (
    'outlined': $color-gray,
    'filled': $color-gray-lightest,
  ),
  'text': (
    'primary': $color-dark,
  ),
  'icons': (
    'active': $color-dark,
  ),
  'dividers': $color-gray-lighten,
  'selection-controls': (
    'thumb': (
      'inactive': $color-gray-lighten,
    ),
    'track': (
      'inactive': $color-white,
    ),
  ),
  'table': (
    'hover': '',
  ),
);
